<template>
  <div class="cardEle">
    <h1>{{msg}}</h1>
    <h2>{{desc}}</h2>
    <label>
      Card number
      <div id='cardNumber' />
    </label>
    <label>
      Expiry
      <div id='expiry' />
    </label>
    <label>
      Cvc
      <div id='cvc' />
    </label>
    <button id='cardele' @click="triggerConfirm()">Confirm</button>
  </div>
</template>

<script>
  import { createElement, confirmPaymentIntent, loadAirwallex, getElement } from 'airwallex-payment-elements';

  const client_secret = 'eyJhbGciOiJIUzI1NiJ9.eyJpYXQiOjE1OTk0ODU5NTcsImV4cCI6MTU5OTQ4OTU1NywiYWNjb3VudF9pZCI6IjM0OWZhYWQ5LTYzZGEtNDg3MS05YTVhLWIyYTIxNjViNTNiNiIsImRhdGFfY2VudGVyX3JlZ2lvbiI6IlVLIiwiaW50ZW50X2lkIjoiaW50X21UY0hROXRuemd0c3llRDVWemptVk12RlgzQyJ9.BY2ZUasLMCsP7ZzOGSU0y6-D-D8ZL_QQ-QwgQ426Iew';
  const intentid = 'int_mTcHQ9tnzgtsyeD5VzjmVMvFX3C';

  loadAirwallex({
    env: 'staging',
    origin: window.location.origin
  }).then(() => {
    const cardNumEle = createElement('cardNumber');
    cardNumEle.mount('cardNumber');
    const cvcEle = createElement('cvc');
    cvcEle.mount('cvc');
    const expiryEle = createElement('expiry');
    expiryEle.mount('expiry');
  })

  const triggerConfirm = async () => {
    try {
      const cardNumEle = getElement('cardNumber');
      const confirmResult = await confirmPaymentIntent({
        element: cardNumEle,
        id: intentid,
        client_secret,
        payment_method_options: {
          card: {
            auto_capture: true
          }
        }
      });
      console.log(`confirm success with ${JSON.stringify(confirmResult)}`);
    } catch (err) {
      console.log(err);
    }
  }

  export default {
    name: 'CardEle',
    data () {
      return {
        msg: 'Airwallex checkout integration',
        desc: 'Option #4: Card elements integration',
        triggerConfirm
      }
    }
  }
</script>



